@tailwind base;
@tailwind components;
@tailwind utilities;

*{
    margin: 0;
    padding: 0;
}

button{
    background-color: blue;
    border: none;
    padding: 4px 9px;
    width: 140px;
    color: white;
    font-size: 18px;
}

@media(0px <= width <= 540px){
    button{
        width: auto;
        margin: 0px 16px;
    }
    .scores{
        gap: 20px;
    }
    .container{
        gap: 20px;
    }
}